@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300&display=swap);
body {
  background-color: #fafbfc;
  font-family: 'Roboto', sans-serif;
}

::-moz-selection{
  background-color: #66ffb3;
  text-decoration: underline;
}

::selection{
  background-color: #66ffb3;
  text-decoration: underline;
}

h1{
  font-family: 'Roboto Condensed', sans-serif;
  color: #1f1f1f;
  font-size: 3rem;
}

h2{
  font-family: 'Roboto Condensed', sans-serif;
  color: #1f1f1f;
  font-size: 2.5rem;
}

.resize{
  width:1.3rem;
}

.main{
  margin: 10%;
}

.main h1{
  font-size: 4rem;
}

.main p{
  font-size: 1.5rem;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
}

span{
  color: #37db89;
}

.portfolio {
  margin: 0% 7% 7% 7%;
}

.tiny{
  color: #999999;
}

.main p.pron{
  font-size: 1rem;
  display: inline-block;
}

.media {
  width:1.6rem;
  height: auto;
}

.font {
  font-size: 1.1rem!important;
}
.navbar-light .navbar-brand {
  color: white;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.5rem;
}

.bg{
  background-color: #1f1f1f;;
}

a {
  color: white;
}
a:hover {
  color: #66ffb3;
}
.navbar-light .navbar-brand:focus, .navbar-light .navbar-brand:hover {
  color: white;
}

.navbar-toggler{
  outline: 0 !important;
}
.navbar-light .navbar-toggler-icon{
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(102, 255, 179, 0.7)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}
.navbar-light .navbar-toggler{
  border-color: #66ffb3;
}

.btn{
  border-radius: 0;
  font-size: 1.3rem;
  border: 2px solid;
}

.btn-outline-success {
  color: #37db89;
  border-color: #37db89;
  margin-right: 1rem;
  margin-top: 1rem;
}

.btn-outline-success:hover{
  background-color: #37db89;
  border-color: #37db89;
}

.card-title.h5{
  color: #37db89;
  font-family: 'Roboto Condensed', sans-serif;
}

.card-columns{
  -webkit-column-count: 4;
     -moz-column-count: 4;
          column-count: 4;
}

@media screen and (max-width: 770px) {
 .card-columns {
    -webkit-column-count: 2;
       -moz-column-count: 2;
            column-count: 2;
  }
  .card-deck .card-columns{
    -webkit-column-count: 2;
       -moz-column-count: 2;
            column-count: 2;
  }
}

.card-deck{
  justify-content: center;
  margin: 0% 2.5%;
}

.card{
  border: none;
}

.card-text{
  font-family: 'Roboto', sans-serif;
}

.main .card-text{
  font-size: 1rem;
}

.card a{
  color: #37db89;
}

.form-control {
  border-radius: 0;
  outline: 0 !important;
}

.form-control.active.focus,
.form-control.active:focus,
.form-control.focus,
.form-control.focus:active,
.form-control:active:focus,
.form-control:focus {
  outline: 0 !important;
  border: 1px solid #ced4da;
  box-shadow: none;
  outline-offset: 0  !important;
  background-image: none  !important;
  box-shadow: none  !important;
}

.col-md-6 .pad{
  padding-right: 10%;
}

.carousel-indicators{
  display: none;
}
